import React from 'react'
import { Box } from '@mui/material'
import TableCrud from '../general/TableCrud'
import PageHeader from '../PageHeader'
import FormModal from '../modal/FormModal'
import DeleteModal from '../modal/DeleteModal'

export default function Crud({
    handleSubmit,
    handleReset,
    handleDeleteConfirm,
    handleFormToggle,
    handleConfirmToggle,
    formPanel,
    formName,
    columns,
    rows,
    headTitle,
    headBtnName,
    headBtnIcon,
    isFormOpen,
    isConfirmOpen,
}) {
    return (
        <Box>
            <PageHeader
                title={headTitle}
                btnName={headBtnName}
                btnClick={handleFormToggle}
                headBtnIcon={headBtnIcon}
            />

            <FormModal
                handleSubmit={handleSubmit}
                handleReset={handleReset}
                formPanel={formPanel}
                formName={formName}
                headTitle={headTitle}
                isModalOpen={isFormOpen}
                handleFormToggle={handleFormToggle}
            />

            <DeleteModal
                isModalOpen={isConfirmOpen}
                handleConfirmToggle={handleConfirmToggle}
                handleDeleteConfirm={handleDeleteConfirm}

            />
            
            <Box sx={{width:'100%',overflowX:'hidden'}}>
                <TableCrud
                    columns={columns}
                    rows={rows}

                />
            </Box>

        </Box>
    )
}
