import React from 'react'
import { Routes, Route, Navigate } from 'react-router-dom';
import { useWrapContent } from '../context/AppWrapper';
import Dashboard from '../pages/Dashboard';
import Login from '../pages/Login';
import Home from '../pages/Dashboard/Home';
import Inventory from '../pages/Dashboard/Inventory';
import Supply from '../pages/Dashboard/Supply';
import Accounts from '../pages/Dashboard/Accounts';
import Setting from '../pages/Dashboard/Setting';
import Users from '../pages/Dashboard/Users';
import { getItem } from '../utils/storage';
import useLogin from '../hooks/useLogin';

export default function AppRoutes() {
  
  const { token } = useLogin();

  return (
    <Routes>
      <Route path='/' element={token || getItem('token') ? <Navigate to={'dashboard'} /> : <Login />} />
      <Route path='dashboard' element={token || getItem('token') ? <Dashboard /> : <Navigate to={'/'} />} >
        <Route path='' element={<Home />} />
        <Route path='users' element={<Users />} />
        <Route path='inventory' element={<Inventory />} />
        <Route path='supply' element={<Supply />} />
        <Route path='account' element={<Accounts />} />
        <Route path='setting' element={<Setting />} />
      </Route>
    </Routes>

  )
}
