import React from 'react';
import { AppBar, Box, Button, Dialog, Grid, IconButton, Slide, Toolbar, Typography } from '@mui/material';
import { Close } from '@mui/icons-material';




const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function FormModal({ handleSubmit, handleReset, formPanel,formName, headTitle, isModalOpen, handleFormToggle }) {

  return (
    <Dialog
      fullScreen
      open={isModalOpen}
      onClose={handleFormToggle}
      TransitionComponent={Transition}
    >
      <AppBar sx={{ position: 'relative' }}>
        <Toolbar>
          <IconButton
            edge="start"
            color="inherit"
            onClick={handleFormToggle}
            aria-label="close"
          >
            <Close />
          </IconButton>
          <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
            {headTitle} Screen
          </Typography>
          <Button autoFocus color="warning" variant='contained' size='small' onClick={handleFormToggle}>
            {formName}
          </Button>
        </Toolbar>
      </AppBar>
      <Box sx={{ display: 'flex', justifyContent: 'center',marginTop:10,padding:{xs:2} }}>
        <form onSubmit={handleSubmit}>
          <Typography variant='h4' sx={{textAlign:'center',marginY:2}}> {formName + ' ' + headTitle}</Typography>
          <Grid container spacing={2}>
            {formPanel}
          </Grid>
          <Box
            sx={{
              display: 'flex',
              justifyContent: 'end',
              gap: 2,
              marginTop: 2
            }}>
            <Button size='small' variant='outlined' color='primary' onClick={handleReset}>Reset</Button>
            <Button size='small' variant='contained' color='primary' onClick={handleSubmit}>{formName}</Button>
          </Box>
        </form>
      </Box>
    </Dialog>

  )
}
