import React, { useEffect, useState } from 'react'
import Crud from '../../../components/crud'
import { Box, Checkbox, FormControlLabel, Grid, IconButton } from '@mui/material'
import InputTextField from '../../../components/general/InputTextField'
import { ArrowDownward, ArrowUpward, Delete, Edit, LocationCity, Payment, Person, Phone, Visibility } from '@mui/icons-material'
import InputSelectField from '../../../components/general/InputSelectField'

const initialFields = {
  isPayment: false,
  user: '',
  bottleIn: '',
  bottleOut: '',
  paymentRecieved: '',
};

const TableRows = [
  { id: 1, name: 'Qaiser Hussain', bottleIn: 5, bottleOut: 5, remainingBalance: 300, phone: '03012709842', address: '35/09/01' },
  { id: 2, name: 'Muttahar Hussain', bottleIn: 5, bottleOut: 5, remainingBalance: 500, phone: '03012709842', address: '35/09/01' },
  { id: 3, name: 'Zeeshan Hussain', bottleIn: 5, bottleOut: 5, remainingBalance: 800, phone: '03012709842', address: '35/09/01' }
];

export default function Supply() {
  const [formName, setFormName] = useState('Add');
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [isConfirmOpen, setIsConfirmOpen] = useState(false);
  const [isStayOpened, setIsStayOpened] = useState(false);
  const [formValue, setFormValue] = useState(initialFields);
  const [row, setRow] = useState({});
  const [rows, setRows] = useState([]);

  let itemPrice = 80;

  const paymentRemaining = (formValue?.bottleOut * itemPrice) - formValue?.paymentRecieved;
  // input fields on change
  const handleChange = (e) => { setFormValue({ ...formValue, [e.target.name]: e.target.value }) }

  const handleCheckBox = (e) => { setFormValue({ ...formValue, [e.target.name]: e.target.checked }); }
  const handleCheckStay = () => { setIsStayOpened(!isStayOpened); }
  // input fields reset
  const handleReset = () => { setFormValue(initialFields) }
  // form open/close
  const handleFormToggle = () => { setIsFormOpen(!isFormOpen) }
  // confirmation open/close
  const handleConfirmToggle = () => { setIsConfirmOpen(!isConfirmOpen) }
  // edit row data
  const handleEdit = (row) => {
    setIsFormOpen(!isFormOpen);
    setFormName('Update');
    setRow(row);
  }
  // view row data
  const handleView = (row) => {
    setIsFormOpen(!isFormOpen);
    setFormName('View');
    setRow(row);
  }

  // delete row
  const handleDelete = (row) => { setIsConfirmOpen(!isConfirmOpen); setRow({ ...row }); }
  // submit data to database
  const handleSubmit = (e) => {
    e.preventDefault();
    const payload = {
      ...formValue,
      paymentRemaining:paymentRemaining
    }
    delete payload?.isPayment;
    console.log(payload);
    handleReset();
  }

  const handleDeleteConfirm = () => {
    // api proceed other work
    console.log(row);
    handleConfirmToggle();
  }

  const formPanel = (
    <>
      {paymentRemaining != 0 ?
        <Box sx={{ color: 'green', textAlign: 'center', flex: 1, marginY: 2 }}>
          {'Today Remaining Payment ' + paymentRemaining + ' (PKR)'}
        </Box>
        :
        null
      }

      <Grid item md={6} sm={6} xs={12}>
        <InputSelectField
          label={'Users'}
          value={formValue?.user}
          onChange={handleChange}
          name={'user'}
          multiline={true}
          required={true}
          list={['qaiser', 'zeeshan', 'pepsi']}
        />
      </Grid>
      <Grid item md={6} sm={6} xs={12}>
        <InputTextField
          label={'Bottle In'}
          value={formValue?.bottleIn}
          onChange={handleChange}
          name={'bottleIn'}
          icon={ArrowDownward}
          iconColor={'green'}
        // required={true}
        />
      </Grid>
      <Grid item md={6} sm={6} xs={12}>
        <InputTextField
          label={'Bottle Out'}
          value={formValue?.bottleOut}
          onChange={handleChange}
          name={'bottleOut'}
          icon={ArrowUpward}
          iconColor={'red'}
        // required={true}
        />
      </Grid>
      <Grid item md={6} sm={6} xs={12}>
        <FormControlLabel control={
          <Checkbox
            checked={formValue?.isPayment ? true : false}
            name='isPayment'
            onChange={handleCheckBox}
            defaultChecked
          />} label="Payment" />
      </Grid>

      {formValue?.isPayment ?
        <Grid item md={6} sm={6} xs={12}>
          <InputTextField
            label={'Payment Recieved'}
            value={formValue?.paymentRecieved}
            onChange={handleChange}
            name={'paymentRecieved'}
            icon={Payment}
            required={true}
          />
        </Grid>
        :
        null}

      {formName == 'Add' ?
        <Grid item md={12} sm={12} xs={12}>
          <FormControlLabel control={
            <Checkbox
              checked={formValue?.isStay}
              name='isStayOpened'
              value={isStayOpened}
              onChange={handleCheckStay}
              defaultChecked
            />} label="Stay Opened" />
        </Grid>
        :
        null}
    </>
  )

  const columns = [
    { id: 1, field: 'name', headerName: 'Name', headerClassName: 'table-head', flex:1, minWidth: 150, },
    { id: 2, field: 'bottleIn', headerName: 'In', headerClassName: 'table-head', width: 30, sortable: false },
    { id: 2, field: 'bottleOut', headerName: 'Out', headerClassName: 'table-head', width: 30, sortable: false },
    { id: 2, field: 'remainingBalance', headerName: 'Balance', headerClassName: 'table-head', flex:1, minWidth: 100, sortable: false },
    { id: 3, field: 'phone', headerName: 'Phone', headerClassName: 'table-head', flex:1, minWidth: 150, sortable: false },
    { id: 4, field: 'address', headerName: 'Address', headerClassName: 'table-head', flex:1, minWidth: 300, sortable: false },
    {
      id: 5, field: 'action', headerName: 'Action', headerClassName: 'table-head', width: 150, sortable: false, headerAlign: 'center',
      renderCell: ({ row }) => (
        <>
          <IconButton onClick={() => handleEdit(row)}><Edit color='warning' /></IconButton>
          <IconButton onClick={() => handleView(row)}><Visibility color='info' /></IconButton>
          <IconButton onClick={() => handleDelete(row)}><Delete color='error' /></IconButton>
        </>
      )
    },
  ];

  useEffect(() => { setRows([...TableRows]) }, []);

  return (
    <div>
      <Crud
        handleSubmit={handleSubmit}
        handleReset={handleReset}
        handleDeleteConfirm={handleDeleteConfirm}
        isFormOpen={isFormOpen}
        isConfirmOpen={isConfirmOpen}
        handleFormToggle={handleFormToggle}
        handleConfirmToggle={handleConfirmToggle}
        formPanel={formPanel}
        formName={formName}
        columns={columns}
        rows={rows}
        row={row}
        submitBtn={'Add'}
        headTitle={'Supply'}
        headBtnName={'Add'}
        headBtnIcon={''}
      />
    </div>
  )
}
