import React from 'react';
import styles from './Navbar.module.css';
import { AccountCircle, Logout, Menu } from '@mui/icons-material';
import { IconButton, styled, Typography, Avatar, Box } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { removeItem, setItem } from '../../../utils/storage';
import { success } from '../../../utils/notifications';

function Navbar({ handleSidebar }) {

  const navigate = useNavigate();
  const handleLogout = () => { removeItem('token'); navigate('/'); success('Logged Out'); }

  return (
    <div className={styles.navbar}>
      <Logo component={'div'} sx={{ position: 'relative' }}>
        <IconButton onClick={handleSidebar}>
          <Menu />
        </IconButton>
        <Box>
          <Typography variant='h6' component={'div'}>
          PREMIUM MINERAL 
          </Typography>
          <Typography component={'span'} sx={{ position: 'absolute', bottom: -3, right: 0, fontSize: 10,color:'red',letterSpacing:3 }}>
           WATER
          </Typography>
        </Box>
      </Logo>
      <IconButton onClick={handleLogout}>
        <Logout />
      </IconButton>
    </div>
  )
}

const Logo = styled(Typography)(({ theme }) => ({
  fontSize: '24px',
  letterSpacing: '1px',
  cursor: 'pointer',
  display: 'flex',
  alignItems: 'center',
  gap: '15px'
}))

export default Navbar