import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Post, login, register } from '../utils/endpoints';
import { getItem, setItem } from '../utils/storage';
import { error, success } from '../utils/notifications';

export default function useHandle() {
    const navigate = useNavigate();
    const initial = { username: '', password: '' };
    const [fields, setFields] = useState(initial);
    const [token, setToken] = useState('');
    const [errors, setErrors] = useState(initial);
    const [show, setShow] = useState(false);
    const [loading, setLoading] = useState(false);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFields({ ...fields, [name]: value });
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        setLoading(true);
        let err = validate(fields);
        if (err.username || err.password) {
            console.log('errors found');
            setLoading(false)
        } else {
            Post(fields, 'login')
                .then(({ data }) => {
                    success('Logged In');
                    setToken(data.token)
                    setItem('username', data.username);
                    setItem('token', data.token);
                    setLoading(false);
                    navigate('/dashboard');
                })
                .catch(err => {
                    setLoading(false);
                    error(err?.response?.data?.error || 'Something went wrong' )
                });
        }
    }

    function validate(data) {
        let err = { username: '', password: '' };
        if (!data.username) {
            err.username = 'username must required'
            // setErrors({...errors,['username']:'username must required'});
        }
        if (!data.password) {
            err.password = 'password must required'
            // setErrors({...errors,['password']:'password must required'});
        }
        setErrors({ ...err });
        return err
    }


    return {
        token,
        loading,
        show,
        errors,
        fields,
        setShow,
        handleChange,
        handleSubmit
    }
} 