import React from 'react'
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
export default function TableCrud({ rows, columns }) {
  return (
    <div style={{ maxHeight: '500px', minWidth: '80%' }}>
      <DataGrid
        columns={columns || []}
        rows={rows || []}
        slots={{
          toolbar: GridToolbar,
        }}
        disableColumnFilter
        disableColumnMenu
        filterMode
        style={{
          width: '100%'
        }}
      />
    </div>
  )
}
