import axios from 'axios';
import {getItem} from './storage';
// const url = 'http://localhost:8000/api'   // development ******************
const url = 'https://elated-colt-moccasins.cyclic.cloud/api' // production ***************
const headers = {Authorization:getItem('token'),'Access-Control-Allow-Origin':'*'}
// const url = process.env.SERVER_URL || 'http://localhost:8000/api'
export const Get = (id,path) => {return axios.get(`${url}/${path}/:${id}`,{headers})}
export const GetAll = (path) => {return axios.get(`${url}/${path}`,{headers})}
export const Post = (payload,path) => {return axios.post(`${url}/${path}`,payload,{headers})}
export const Update = (id,path) => {return axios.post(`${url}/${path}/:${id}`,{headers})}
export const Delete = (id,path) => {return axios.post(`${url}/${path}/:${id}`,{headers})}


