import React from 'react';
import { InputAdornment, TextField } from '@mui/material';
import propTypes from 'prop-types';
import { QuestionMark } from '@mui/icons-material';

const InputTextField = ({ label, value, onChange, variant, name, icon: Icon, iconColor, iconPosition, required }) => {
    return (
        <TextField
            label={label || 'Demo'}
            value={value || ''}
            onChange={onChange || ''}
            variant={variant || 'filled'}
            name={name || ''}
            type='text'
            InputProps={{
                endAdornment: <InputAdornment position={iconPosition || 'end'} > {Icon ? <Icon style={{color:iconColor}} /> : <QuestionMark />} </InputAdornment>,
            }}
            fullWidth
            required={required}
        />
    )
}

InputTextField.propTypes = {
    label: propTypes.string,
    value: propTypes.string,
    onChange: propTypes.func,
    variant: propTypes.string,
    name: propTypes.string,
    icon: propTypes.func,
    iconPosition: propTypes.string,
    required: propTypes.bool
}

export default InputTextField;


