import { Box, Button, Typography } from '@mui/material'
import React from 'react'

export default function PageHeader({ title, btnName, btnClick }) {
    return (
        <Box
            sx={{
                display: 'flex',
                justifyContent: 'space-between',
                margin:'10px 0 20px 0'
            }}>
            <Typography variant='h5'>
                {title} Detail
            </Typography>
            <Button
                color={'primary'}
                variant='contained'
                size={'small'}
                onClick={btnClick}
            >
                {btnName}
            </Button>
        </Box>
    )
}
