import React from 'react';
import { AppBar, Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Grid, IconButton, Slide, Toolbar, Typography } from '@mui/material';
import { Close } from '@mui/icons-material';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function DeleteModal({ isModalOpen, handleConfirmToggle, handleDeleteConfirm }) {

  return (
    <Dialog
      open={isModalOpen}
      TransitionComponent={Transition}
      keepMounted
      onClose={handleConfirmToggle}
      aria-describedby="alert-dialog-slide-description"
    >
      <DialogTitle sx={{ color: 'red' }}>Delete Confirmation !</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-slide-description">
          Are you sure you want to delete ?
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleConfirmToggle} color='inherit' variant='contained'>Cancel</Button>
        <Button onClick={handleDeleteConfirm} color='error' variant='contained'>Proceed</Button>
      </DialogActions>
    </Dialog>

  )
}
