import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useNavigate } from 'react-router-dom';
import { login, register } from '../../utils/endpoints';
import { Box, Typography, TextField, Button, InputAdornment, IconButton, CircularProgress } from '@mui/material';
import { LoginContainer, LoginForm, Loader } from './style';
import useLogin from '../../hooks/useLogin';
import { LockOpen, Person, Visibility, VisibilityOff, Login as LoginIcon } from '@mui/icons-material';


export default function Login() {

  const { loading, show, errors, fields, setShow, handleChange, handleSubmit } = useLogin();

  return (

    <LoginContainer>
      <LoginForm component={'form'} onSubmit={handleSubmit}>
        {
          loading &&
          <Loader>
            <CircularProgress color='info' />
          </Loader>
        }

        <Box textAlign={'center'}><LockOpen fontSize='large' /></Box>
        <TextField
          label='Username'
          value={fields.username}
          onChange={handleChange}
          variant='filled'
          name='username'
          type='text'
          helperText={<Typography color={'red'}>{errors.username && errors.username}</Typography>}
          InputProps={{
            endAdornment: <InputAdornment position="end"><Person /> </InputAdornment>,
          }}

        // required
        />
        <TextField
          label='Password'
          value={fields.password}
          onChange={handleChange}
          variant='filled'
          name='password'
          type={show ? 'text' : 'password'}
          helperText={<Typography color={'red'}>{errors.password && errors.password}</Typography>}
          InputProps={{
            endAdornment: <InputAdornment position="end">
              {show
                ?
                <IconButton style={{ padding: 0 }} onClick={() => setShow(false)}><VisibilityOff /></IconButton>
                :
                <IconButton style={{ padding: 0 }} onClick={() => setShow(true)}><Visibility /></IconButton>
              }
            </InputAdornment>,
          }}
        // required
        />

        <Button variant='contained' type='submit' startIcon={<LoginIcon />}>
         Login
        </Button>

      </LoginForm>
    </LoginContainer>

  )
}
