import { ArrowDownward, ArrowUpward, LocalDrink, Person } from '@mui/icons-material'
import { Box, Grid, Paper, Typography } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'

export default function Home() {
  return (
    <Grid container spacing={2} sx={{ display: 'flex' }}>

      <Grid item xs={12} sm={6} md={4}>
        <Paper elevation={2} sx={{ padding: 2 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
            <Typography variant='h3'>28</Typography>
            <Box>
              <Person style={{ height: '50px', width: '50px' }} />
            </Box>
          </Box>
          <Box sx={{ display: 'flex', justifyContent: 'end' }}>
            <Typography variant='h6'>Clients</Typography>
          </Box>
        </Paper>
      </Grid>

      <Grid item xs={12} sm={6} md={4}>
        <Paper elevation={2} sx={{ padding: 2,position:'relative' }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
            <Typography variant='h3'>188</Typography>
            <Box>
              <LocalDrink style={{ height: '50px', width: '50px' }} />
            </Box>
          </Box>
          <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
            <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
              <Box sx={{ display: 'flex', gap: 1, alignItems: 'center', }} >
                <ArrowDownward style={{ color: 'green',fontSize:18 }} />
                <Typography style={{ fontSize:14 }}>100</Typography>
              </Box>
              <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
                <ArrowUpward style={{ color: 'red', fontSize:18 }} />
                <Typography>88</Typography>
              </Box>
            </Box>
            <Typography variant='h6'>Bottles</Typography>
          </Box>
        </Paper>
      </Grid>

    </Grid>
  )
}
